span {
  position: absolute;
  margin-left: 20px;
  cursor: pointer;
}

#li:hover {
  background-color: rgb(198, 212, 224);
}

#item {
  position: absolute;
  right: 30px;
}

input[name="task"] {
  width: 100%;
  background-color: transparent;
  border: 0;
  background-color: none;
}

input[name="task"]:focus {
  outline: none;
}

.wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: auto;
  background-color: blueviolet;
}

#text{
  width: 88% !important;
}
#add{
  position: absolute;
  right: 1rem;
  width: 10%;
}

#form-inline{
  width: 100% !important;
}
